.sidebar {
  margin-bottom: 40px;

  @include for-tablet-up {
    margin-bottom: 100px;
  }

  &__container {
    @include for-tablet-up {
      display: flex;
      justify-content: space-between;
      gap: 32px;     
    }
    @include for-desktop-up {
      display: block;
    }
  }
}

.categories {
  margin-bottom: 40px;

  @include for-tablet-up {
    margin-bottom: 0;
  }

  @include for-desktop-up {
    margin-bottom: 86px;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 24px;
    width: 100%;
    height: 228px;
    overflow-y: auto;
    font-family: 'DM Sans';
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: -0.02em;
    color: var(--categories-light-text);

    @include for-tablet-up {
      height: 100%;
      width: 337px;
      font-size: 18px;
      line-height: 24px;
      gap: 32px;
    }

    @include for-desktop-up {
      width: 100%;
      height: 472px;
    }
  }

  &__current {
    text-transform: uppercase;
    color: var(--accent-violet-color);
    text-shadow: 0px 0px 0 var(--accent-violet-color),
      0px 0px 0 var(--accent-violet-color), 0px 0px 0 var(--accent-violet-color),
      0px 0px 0 var(--accent-violet-color);
    transition: text-shadow 1s;
  }
  &__item {
    cursor: pointer;
  }
}
